.property-wrapper {
    width: 100%;
    padding: 30px 50px;
    .property-image-map-section {
        width: 100%;
        padding: 30px 0;
        display: flex;
        justify-content: space-between;
        .property-image {
            width: 50%;
            img {
                width: 100%;
                height: 350px;
                object-fit: cover;
                border-radius: 10px;
            }
        }
        .property-map {
            width: 47%;
            margin-left: 20px;
            border-radius: 10px;
            overflow: hidden;
            img {
                width: 100%;
            }
        }
    }

    .property-title {
        width: 100%;
        .liked-wrapper {
            width: 40px;
            height: 40px;
        }
    }
    .property-description {
        width: 80%;
        .space-list {
            .space-item {
                display: flex;
                align-items: center;
                margin-bottom: 20px;
                img {
                    width: 120px;
                    margin-right: 10px;
                    object-fit: cover;
                    border-radius: 10px;
                }
            }
        }
    }
    .uploaded-image {
        width: 60px;
        height: 60px;
        border-radius: 500px;
        object-fit: cover;
    }
}

@media (min-width: 961px) {
    /* tablet, landscape iPad, lo-res laptops ands desktops */
}

@media (max-width: 500px) {
    /* portrait e-readers (Nook/Kindle), smaller tablets @ 600 or @ 640 wide. */
    /* tablet, landscape iPad, lo-res laptops ands desktops */
    .property-wrapper {
        width: 100%;
        padding: 30px 20px;
        .property-image-map-section {
            width: 100%;
            padding: 30px 0;
            display: flex;
            flex-direction: column;
            justify-content: space-between;

            .property-image {
                width: 100%;
                img {
                    width: 100%;
                    height: 350px;
                    object-fit: cover;
                    border-radius: 10px;
                }
            }
            .property-map {
                width: 100%;
            }
        }
    }
}
